@import 'global/styles/Colors.module.css';
@import 'global/styles/Fonts.module.css';

:root {
  --body_background: white;
  --body_color: black;
  /* Shadow Box */
  --webkit_box_shadow: 0 1px 9px 0 rgba(0, 0, 0, 0.14);
  --moz_box_shadow: 0 1px 9px 0 rgba(0, 0, 0, 0.14);
  --box_shadow: 0 1px 9px 0 rgba(0, 0, 0, 0.14);
  /*  Text Color  */
  --color_text: var(--gray-600);
  --hover_active: rgba(97, 97, 97, 0.73);
  --link_visited: var(--gray-700);
  --link_color: whitesmoke;
  --color_shimmer_text: rgba(0, 0, 0, 0.14);
  --image_shimmer_color: rgba(0, 0, 0, 0.07);
  --background_shimmer: linear-gradient(
    -45deg,
    #eee 40%,
    #fafafa 50%,
    #eee 60%
  );
}

[data-theme='light'] {
  --body_background: white;
  --body_color: black;
  /* Shadow Box */
  --webkit_box_shadow: 0 1px 9px 0 rgba(0, 0, 0, 0.14);
  --moz_box_shadow: 0 1px 9px 0 rgba(0, 0, 0, 0.14);
  --box_shadow: 0 1px 9px 0 rgba(0, 0, 0, 0.14);
  /*  Text Color  */
  --color_text: var(--gray-600);
  --hover_active: rgba(97, 97, 97, 0.73);
  --link_visited: var(--gray-700);
  --link_color: whitesmoke;
  --color_shimmer_text: rgba(0, 0, 0, 0.14);
  --image_shimmer_color: rgba(0, 0, 0, 0.07);
  --background_shimmer: linear-gradient(
    -45deg,
    #eee 40%,
    #fafafa 50%,
    #eee 60%
  );
}

[data-theme='dark'] {
  --body_background: #282c34;
  --body_color: white;
  /* Shadow Box */
  --webkit_box_shadow: 0 1px 9px 0 var(--gray-500);
  --moz_box_shadow: 0 1px 9px 0 var(--gray-500);
  --box_shadow: 0 1px 9px 0 var(--gray-500);
  /*  Text Color  */
  --color_text: whitesmoke;
  --hover_active: var(--gray-300);
  --link_visited: whitesmoke;
  --link_color: whitesmoke;
  --color_shimmer_text: var(--gray-500);
  --image_shimmer_color: var(--gray-500);
  --background_shimmer: linear-gradient(
    -45deg,
    var(--gray-300) 40%,
    var(--gray-200) 50%,
    var(--gray-300) 60%
  );
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  background: var(--main-bg-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
