    .main_item_products_print_item {
        grid-template-columns: repeat(4, 1fr);
    }

    .main_item_products_graphic_design_items {
        grid-template-columns: repeat(4, 1fr);
    }

    .main_item_products_artwork_page {
        grid-template-columns: repeat(4, 1fr);
    }

    .image_background_homePage {
        height: 100dvh;
        background-size: cover;
        object-fit: cover;
    }

    .home_image_bg {
        width: 100%;
        height: 100vh;
        object-fit: cover;
        background-size: contain;
    }

    .home_main_text {
        top: 20%;
        left: 5%;
        font-size: calc(9vw + 4vh);
        width: fit-content;
        display: block;
