@media (min-width: 60em) and (max-width: 80em) {
    .image_background_homePage {
        height: 100dvh;
        background-size: cover;
        object-fit: cover;
    }

    .main_item_products_print_item {
        grid-template-columns: repeat(2, 1fr);
    }

    .main_item_products_graphic_design_items {
        grid-template-columns: repeat(2, 1fr);
    }

