    .aboutPageTextPOne {
        font-size: 1.35rem; /*calc(0.8vw + 0.8vh);*/
        font-weight: 700;
    }
}

@media (min-width: 40em) and (max-width: 60em) {

    .mobile_btn {
        display: block;
        position: absolute;
        top: 1rem;
        right: 1rem;
        color: var(--color_text);
        cursor: pointer;
        z-index: 10;
    }

    .header_item_home {
        display: block;
    }

    .header_component {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .header_navbar_menu_item {
        gap: 1rem;
        display: flex;
        flex-direction: column;
        position: fixed;
        left: -100%;
        top: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100vh;
        background: var(--body_background);
        z-index: 10;
        transition: left 1s;
        justify-content: space-evenly;
        align-content: center;
        align-items: center;
        text-align: center;
        padding-top: 1rem;
        padding-right: 3rem;
